



























export default {
	data() {
		return {
			name : '',
			pass : '',
			modalName: null,
			modalcontent:''
		};
	},
	onLoad:function(){
		
	},
	methods: {
		login(e) {
			var url = 'login/';
			this.urlRequest({
			url: url,
			data: {
				username : this.name,
				password : this.pass
			},
			method: 'POST',
			success: res => {
				console.log(res);
				if (res.statusCode == 200) {
					var token = res.data.token;
					// 保存用户token
					uni.setStorageSync("token", token);
					// 切换页面跳转，使用tab切换的api
					uni.switchTab({
						url: "../tabbar/tabbar-5/tabbar-5",
						// success() {
						// 	
						// }
					});
				}
				else if(res.statusCode == 400){
					var msg = '账号或密码错误，请重新输入';
					this.showModal(msg);
				}
			},
			fail: () => {},
			complete: () => {}
		});
		},
		showModal(msg) {
			this.modalName = 'Modal';
			console.log(this.modalName);
			this.modalcontent = msg;
		},
		hideModal(e) {
			this.modalName = null
		},
	},
}
